<template>
  <div class="search">
    <div class="loading" v-show="loading">
      <img src="../imgs/loading.gif" alt="" />
    </div>

    <SearchInput
      @add-result="getSearchResult"
      @yes-result="getSearchList"
      @clear-value="clearValue"
      :records="records"
      @clear-records="clearRecords"
      :intValue="intValue"
    />

    <section class="rmss" v-show="ckRmss">
      <h3>热门搜索</h3>
      <ul class="search-cs">
        <SearchCard
          :intValue="intValue"
          @get-card="getSearchList"
          v-for="(card, first) in cards"
          :key="first"
          :card="card"
          :ckSs="ckSs"
        />
      </ul>
    </section>

    <section class="history" v-show="ckRmss">
      <h3>历史搜索</h3>
      <ul class="history-search">
        <HistorySearch
          :intValue="intValue"
          @clear-history="clearHistory"
          v-for="(history, i) in historys"
          :key="i"
          :history="history"
          @get-history="getSearchList"
        />
      </ul>
    </section>

    <section class="ss" v-if="ckSs && intValue">
      <h3>搜索"{{ intValue }}"</h3>
      <ul class="search-result">
        <SearchResult
          :intValue="intValue"
          v-for="(result, keyword) in results"
          :key="keyword"
          :result="result"
          @get-result="getSearchList"
        />
      </ul>
    </section>

    <section class="qrss" v-show="ckQrss && intValue">
      <ul class="search-record" @click="$emit('updata-song-item', records)">
        <SearchRecord
          :intValue="intValue"
          v-for="record in records"
          :key="record.id"
          :record="record"
          @updata-song="$emit('updata-song', record)"
          :songId="songId"
          :playing="playing"
        />
      </ul>
    </section>
  </div>
</template>

<script>
import SearchInput from "@/components/SearchInput.vue";
import SearchResult from "@/components/SearchResult.vue";
import SearchCard from "@/components/SearchCard.vue";
import SearchRecord from "@/components/SearchRecord.vue";
import HistorySearch from "@/components/HistorySearch.vue";

// var _ = require("lodash");

export default {
  name: "SearchView",
  components: {
    SearchInput,
    SearchResult,
    SearchCard,
    SearchRecord,
    HistorySearch,
  },
  props: {
    song: Object,
    songId: [Number, String],
    playing: Boolean,
  },
  data: function () {
    return {
      cards: [],
      results: [],
      intValue: "",
      records: null,
      historys: JSON.parse(localStorage.getItem("historys")) || [],
      ckRmss: true,
      ckSs: false,
      ckQrss: false,
      loading: false,
    };
  },

  methods: {
    getSearchResult: function (keywords) {
      this.loading = true;
      this.intValue = keywords;
      this.ckRmss = false;
      this.ckSs = true;
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: { keywords, type: "mobile" },
        })
        .then((res) => {
          this.loading = false;
          this.intValue
            ? (this.results = res.data.result.allMatch)
            : (this.results = []);
        });
    },

    getSearchList: function (keywords) {
      this.loading = true;
      this.intValue = keywords;
      this.ckQrss = true;
      this.ckSs = false;
      this.ckRmss = false;
      this.axios
        .get("https://apis.netstart.cn/music/search", {
          params: { keywords, limit: 10, offset: 0, type: 1018 },
        })
        .then((res) => {
          this.loading = false;
          this.records = res.data.result.song.songs;
          console.log(res.data.result.song.songs);
        });

      this.historys = [...new Set([keywords, ...this.historys])];
    },

    clearValue: function () {
      this.intValue = "";
      this.ckSs = false;
    },

    clearHistory: function (history) {
      this.historys = this.historys.filter((o) => o !== history);
    },

    clearRecords: function () {
      this.records = null;
    },
  },

  created() {
    this.loading = true;
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.loading = false;
      this.cards = res.data.result.hots;
    });

    // this.getSearchResult = _.debounce(this.getSearchResult, 800);
  },

  watch: {
    intValue: function (newValue) {
      if (newValue) {
        this.ckSss = false;
      } else {
        this.ckRmss = true;
        this.ckSss = false;
      }
    },

    historys: function (newHistory) {
      localStorage.setItem("historys", JSON.stringify(newHistory));
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  padding-top: 40rem;
  background-color: #fcfcfd;

  .loading {
    width: 50rem;
    position: absolute;
    right: 0;
    left: 0;
    top: 50%;
    bottom: 0;
    margin: auto;
    z-index: 1;
  }

  .rmss {
    padding: 15rem 10rem 0;
    background-color: #fcfcfd;

    h3 {
      font-size: 12rem;
      line-height: 12rem;
      color: #666;
    }

    .search-cs {
      margin: 15rem 0;
    }
  }

  .history {
    padding: 10rem 10rem 0;
    background-color: #fcfcfd;
  }

  .ss {
    background-color: #fcfcfd;
    h3 {
      height: 50rem;
      margin-left: 10rem;
      padding-right: 10rem;
      font-size: 15rem;
      line-height: 50rem;
      color: #507daf;
      border-bottom: 1rem solid #ededed;
    }
  }
}
</style>
