<template>
  <li
    @click="
      value = history;
      $emit('get-history', history);
    "
  >
    <i></i>
    <span>{{ history }}</span>
    <b @click.stop="$emit('clear-history', history)"> X</b>
  </li>
</template>

<script>
export default {
  props: {
    history: {},
  },
};
</script>

<style lang="less" scoped>
li {
  height: 45rem;
  display: flex;
  align-items: center;

  i {
    width: 15rem;
    height: 15rem;
    background-image: url("../imgs/jilu.svg");
    background-repeat: no-repeat;
  }

  span {
    font-size: 16rem;
    padding-left: 15rem;
  }

  b {
    font-size: 15rem;
    position: absolute;
    right: 20rem;
  }
}
</style>
