<template>
  <li
    class="home-card"
    @click="$router.push({ path: '/playlist', query: { id: item.id } })"
  >
    <img :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=247x0`" alt="" />
    <p>{{ item.name }}</p>
    <span>{{ playCount(item.playCount) }}</span>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
  },
  methods: {
    playCount: function (item) {
      if (item > 100000000) {
        return Math.floor(item / 10000000) + "亿";
      } else {
        return Math.floor(item / 1000) / 10 + "万";
      }
    },
  },
};
</script>

<style lang="less" scoped>
li.home-card {
  width: 30%;
  flex-grow: 1;
  position: relative;

  &:nth-child(3n + 2) {
    margin: 0 5rem;
    margin-bottom: 15rem;
  }

  p {
    padding: 0 5rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }

  span {
    color: white;
    padding-left: 12px;
    position: absolute;
    right: 5rem;
    top: 2rem;
    background-image: url("../imgs/耳机logo.svg");
    background-repeat: no-repeat;
    background-position: 0;
    background-size: 11rem 10rem;
  }
}
</style>
