<template>
  <div class="int">
    <div class="cover">
      <i class="icon"></i>
      <input
        type="text"
        placeholder="搜索歌曲、歌手、专辑"
        v-model="value"
        ref="Intfocus"
        @keyup="getSearchResult"
        @keyup.enter="getSearchList(value)"
      />
      <div
        class="clear"
        v-show="value"
        @click="
          $emit('clear-records', records);
          clearValue();
        "
      >
        <i class="empty"></i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      value: "",
    };
  },

  props: {
    records: Array,
    intValue: String,
  },
  methods: {
    getSearchResult: function () {
      this.$emit("add-result", this.value);
    },

    getSearchList: function () {
      this.$emit("yes-result", this.value);
    },

    clearValue: function (value) {
      this.value = "";
      this.$emit("clear-value", value);
    },
  },

  mounted() {
    this.$refs.Intfocus.focus();
  },

  watch: {
    intValue: function () {
      this.value = this.intValue;
    },
  },
};
</script>

<style lang="less" scoped>
.int {
  padding: 15rem 10rem;
  border-bottom: 1rem solid #ededed;
  background-color: #fcfcfd;

  .cover {
    position: relative;
    width: 100%;
    height: 30rem;
    padding: 0 30rem;
    background: #ebecec;
    border-radius: 30rem;

    .icon {
      width: 13rem;
      height: 13rem;
      position: absolute;
      left: 0;
      top: 9rem;
      margin: 0 8rem;
      vertical-align: middle;
      background-image: url("../imgs/icon-search.svg");
    }
    input {
      width: 100%;
      height: 30rem;
      line-height: 18rem;
      background: rgba(0, 0, 0, 0);
      font-size: 14rem;
      border: none;
      outline: 0;
    }

    .clear {
      position: absolute;
      right: 25rem;
      top: 0;
      width: 30rem;
      height: 30rem;
      line-height: 28rem;
      text-align: center;

      .empty {
        width: 16rem;
        height: 16rem;
        padding-right: 10rem;
        display: inline-block;
        background-image: url("../imgs/icon-clear.svg");
        background-repeat: no-repeat;
        vertical-align: middle;
        cursor: pointer;
      }
    }
  }
}
</style>
