<template>
  <div class="playpage">
    <div class="bg">
      <div
        v-if="song.al"
        class="tmp"
        :style="`background:url(${song?.al.picUrl}) center no-repeat;background-size: cover`"
      ></div>
      <div
        v-else
        class="tmp"
        :style="`background:url(${song?.picUrl}) center no-repeat;background-size: cover`"
      ></div>
    </div>

    <header>
      <h2>{{ song.name }}</h2>
      <h4 v-if="song.al">{{ song?.ar[0].name }} - {{ song?.al?.name }}</h4>
      <h4 v-else>
        {{ song?.song?.artists[0]?.name }} - {{ song?.song?.album?.name }}
      </h4>
      <img
        src="../imgs/隐藏.png"
        alt=""
        @click="$parent.showPlayPage = false"
      />
      <i class="fa fa-share-alt" aria-hidden="true"></i>
    </header>
    <section>
      <PlayDisc
        v-if="showDisc"
        @click.native="showDisc = false"
        :song="song"
        :playing="playing"
      />
      <PlayLyric
        v-else
        @click.native="showDisc = true"
        :song="song"
        :duration="duration"
        :currentTime="currentTime"
      />
    </section>
    <footer>
      <input
        v-if="duration"
        type="range"
        name=""
        id=""
        min="0"
        :max="duration"
        :value="tmpCurrentTime"
        @input="draging = true"
        @change="
          draging = false;
          $emit('updata-time', $event.target.value);
        "
      />
      <div class="song-time">
        <span>{{ cTime }}</span>
        <span>{{ dTime }}</span>
      </div>
      <br />
      <div class="btns">
        <span
          @click="$emit('change-model')"
          class="iconfont"
          v-if="model == '顺序播放'"
          >&#xe68d;</span
        >
        <span
          @click="$emit('change-model')"
          v-show="model == '随机播放'"
          class="iconfont"
          >&#xe622;</span
        >
        <span
          @click="$emit('change-model')"
          v-if="model == '单曲循环'"
          class="iconfont"
          >&#xe68c;</span
        >
        <span @click="$emit('change-song', 'prev')">
          <i class="fa fa-step-backward" aria-hidden="true"></i>
        </span>
        <span v-if="!playing" @click.stop="$emit('play-song')">
          <i class="fa fa-play-circle-o z" aria-hidden="true"></i>
        </span>
        <span v-else @click.stop="$emit('pause-song')">
          <i class="fa fa-pause-circle-o z" aria-hidden="true"></i>
        </span>
        <span @click="$emit('change-song', 'next')">
          <i class="fa fa-step-forward" aria-hidden="true"></i>
        </span>
        <span @click.stop="$emit('toggle-showSongList')">
          <i class="fa fa-bars" aria-hidden="true"></i>
        </span>
      </div>
    </footer>
  </div>
</template>

<script>
import PlayDisc from "@/components/PlayDisc.vue";
import PlayLyric from "@/components/PlayLyric.vue";
export default {
  components: {
    PlayDisc,
    PlayLyric,
  },
  props: {
    song: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    model: String,
  },
  data: function () {
    return {
      showDisc: true,
      draging: false,
      tmpCurrentTime: this.currentTime,
      love: false,
      cTime: "00:00",
      dTime: "00:00",
    };
  },

  mounted() {
    const musicTime = this.duration;
    const branch = Math.floor(musicTime / 60);
    const second = Math.ceil(musicTime % 60);
    if (branch < 10 && second < 10) {
      this.dTime = `0${branch}:0${second}`;
    } else if (branch < 10) {
      this.dTime = `0${branch}:${second}`;
    } else if (second < 10) {
      this.dTime = `${branch}:0${second}`;
    } else {
      this.dTime = `${branch}:${second}`;
    }
  },

  watch: {
    currentTime: function (n) {
      if (!this.draging) {
        this.tmpCurrentTime = n;
        const stopTime = this.currentTime;
        const branch = Math.floor(stopTime / 60);
        const second = Math.floor(stopTime % 60);
        if (branch < 10 && second < 10) {
          this.cTime = `0${branch}:0${second}`;
        } else if (branch < 10) {
          this.cTime = `0${branch}:${second}`;
        } else if (second < 10) {
          this.cTime = `${branch}:0${second}`;
        } else {
          this.cTime = `${branch}:${second}`;
        }
      }
      const stopTime = this.currentTime;
      const branch = Math.floor(stopTime / 60);
      const second = Math.floor(stopTime % 60);
      if (branch < 10 && second < 10) {
        this.cTime = `0${branch}:0${second}`;
      } else if (branch < 10) {
        this.cTime = `0${branch}:${second}`;
      } else if (second < 10) {
        this.cTime = `${branch}:0${second}`;
      } else {
        this.cTime = `${branch}:${second}`;
      }
    },
  },
};
</script>

<style lang="less" scoped>
@font-face {
  font-family: "iconfont";
  src: url("../图标/iconfont.ttf") format("truetype");
}

.iconfont {
  font-family: "iconfont";
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.playpage {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 666;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  .bg {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    overflow: hidden;
  }

  .bg::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
  }

  .tmp {
    width: 120%;
    height: 100%;
    filter: blur(5rem);
    transform: scale(1.1);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
  }

  header {
    width: 100vw;
    h2 {
      padding-top: 5rem;
      font-size: 24rem;
      color: white;
      text-align: center;
    }

    h4 {
      padding-top: 5rem;
      font-size: 12rem;
      color: white;
      text-align: center;
    }

    img {
      display: block;
      width: 50rem;
      position: absolute;
      top: 5rem;
      left: 0;
      padding-right: 15rem;
    }
    i {
      font-size: 30rem;
      color: white;
      position: absolute;
      top: 10rem;
      right: 10rem;
    }
  }

  section {
    width: 100vw;
    flex-grow: 1;
  }

  footer {
    position: relative;
    text-align: center;
    width: 100vw;
    padding-bottom: 60rem;

    input {
      width: 75%;
      height: 5rem;
      position: absolute;
      top: 5rem;
      left: 48rem;
    }

    .song-time {
      display: flex;
      justify-content: space-between;
      align-items: center;

      span {
        font-size: 12rem;
        color: white;
        padding: 0 8rem;
      }
    }

    .btns:nth-child(1) {
      padding-bottom: 15rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .btns {
      padding: 5rem 0;
      display: flex;
      justify-content: space-between;
      align-items: center;

      span {
        font-size: 24rem;
        color: white;
        padding: 0 10rem;

        i {
          font-size: 25rem;
        }

        .z {
          font-size: 40rem;
        }
      }

      .active {
        color: red;
      }
    }
  }
}
</style>
