<template>
  <div class="songlist">
    <ul class="card">
      <h3>播放列表</h3>
      <li
        v-for="song in songlist"
        :key="song.id"
        :style="{ color: song.id === songId ? 'red' : '' }"
        @click="$emit('updata-song', song)"
      >
        <div class="sgleft">
          <h5>{{ song.name }}</h5>
          <span v-if="song.al"
            >{{ song?.ar[0].name }} - {{ song?.al?.name }}</span
          >
          <span v-else
            >{{ song?.song?.artists[0]?.name }} -
            {{ song?.song?.album?.name }}</span
          >
        </div>
        <div class="sgright" v-if="song.id !== songId"></div>
        <div class="sgright active" :class="{ playing: playing }" v-else>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </li>
    </ul>
    <div class="mask" @click="$emit('toggle-showSongList')"></div>
  </div>
</template>

<script>
export default {
  props: {
    songlist: Array,
    songId: [Number, String],
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
@keyframes running {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(60deg);
  }
}

.songlist {
  width: 100%;
  height: 90vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 777;
  padding: 15rem;
  display: flex;
  justify-content: center;
  align-items: center;

  .card {
    flex: 1;
    background-color: white;
    min-height: calc(60vh - 30rem);
    padding: 15rem;
    border-radius: 15rem;
    z-index: 6;
    height: 80vh;
    overflow: scroll;

    h3 {
      text-align: center;
      font-size: 20rem;
      padding-bottom: 15rem;
    }

    li {
      padding: 5rem 0;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .sgleft {
        padding: 8rem 0;

        h4 {
          font-size: 17rem;
        }

        .info {
          .left {
            display: inline-block;
            width: 12rem;
            height: 8rem;
            margin-right: 4rem;
            background-image: url("../imgs/index_icon_2x.png");
            background-size: 166rem 97rem;
          }

          span {
            font-size: 12rem;
            color: #888;
          }
        }
      }

      .sgright {
        width: 22rem;
        height: 22rem;
        margin-right: 15rem;
        background-position: -24rem 0;
        background-image: url("../imgs/index_icon_2x.png");
        background-size: 166rem 97rem;

        &.active {
          background-image: none;
          display: flex;
          justify-content: space-around;

          span {
            width: 11%;
            height: 100%;
            background-color: red;
            animation: running 0.6s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;

            &:nth-child(1) {
              animation-delay: 0s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.4s;
            }
            &:nth-child(4) {
              animation-delay: -0.6s;
            }
          }
          &.playing {
            span {
              animation-play-state: running;
            }
          }
        }
      }

      h5 {
        font-size: 16rem;
      }
    }
  }

  .mask {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 7rem;
    left: 0;
    z-index: -1;
  }
}
</style>
