<template>
  <li
    @click="
      value = card.first;
      $emit('get-card', value);
    "
  >
    <span>{{ card.first }}</span>
  </li>
</template>

<script>
export default {
  data: function () {
    return {
      value: "",
    };
  },
  props: {
    card: Object,
  },
};
</script>

<style lang="less" scoped>
li {
  display: inline-block;
  height: 32rem;
  margin-right: 8rem;
  margin-bottom: 8rem;
  padding: 0 14rem;
  font-size: 14rem;
  line-height: 32rem;
  border: 1rem solid #d3d4da;
  border-radius: 32rem;
}
</style>
