<template>
  <div class="playbar" v-if="song">
    <img
      v-if="song.al"
      :class="{ playing: playing }"
      :src="`${song?.al?.picUrl}?imageView=1&type=webp&thumbnail=40x0`"
      alt=""
    />
    <img
      v-else
      :class="{ playing: playing }"
      :src="`${song?.picUrl}?imageView=1&type=webp&thumbnail=40x0`"
      alt=""
    />
    <h3 v-if="song.al">{{ song?.name }} - {{ song?.ar[0].name }}</h3>
    <h3 v-else>{{ song?.name }} - {{ song?.song?.artists[0]?.name }}</h3>
    <div class="btn">
      <button v-if="!playing" @click.stop="$emit('play-song')">
        <i class="fa fa-play" aria-hidden="true"></i>
      </button>
      <button v-else @click.stop="$emit('pause-song')">
        <i class="fa fa-pause" aria-hidden="true"></i>
      </button>
      <canvas ref="canvas" width="30" height="30"></canvas>
    </div>
    <i
      class="fa fa-bars"
      aria-hidden="true"
      @click.stop="$emit('toggle-showSongList')"
    ></i>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      playlength: null,
    };
  },
  props: {
    song: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
  },

  computed: {
    getPlayLength: function () {
      return this.currentTime / this.duration;
    },
  },

  watch: {
    getPlayLength: function (n) {
      var canvas = this.$refs.canvas;
      var context = canvas.getContext("2d");

      context.clearRect(0, 0, 50, 50);

      context.beginPath();
      context.arc(15, 15, 14, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      context.strokeStyle = "gray";
      context.lineWidth = 3;
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes rotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playbar {
  width: 100%;
  height: 60rem;
  padding: 0 15rem;
  background-color: white;
  z-index: 555;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;

  img {
    width: 60rem;
    border-radius: 50%;
    animation: rotate 8s linear infinite;
    animation-play-state: paused;
    // border: 10rem solid black;

    &.playing {
      animation-play-state: running;
    }
  }

  h3 {
    font-size: 14rem;
    font-weight: 600;
  }

  .btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    button {
      border: none;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      background-color: transparent;
      outline: none;

      i {
        font-size: 16rem;
      }
    }
  }

  i {
    font-size: 20rem;
  }
}
</style>
