<template>
  <div v-if="playlist" class="playlist">
    <div class="loading" v-show="loading">
      <img src="../imgs/loading.gif" alt="" />
    </div>
    <header>
      <div
        v-if="playlist.coverImgUrl"
        class="bg"
        :style="`background:url(${playlist?.coverImgUrl}) center no-repeat;background-size: cover`"
      ></div>
      <div class="header-img">
        <img
          :src="
            playlist.coverImgUrl
              ? `${playlist?.coverImgUrl}?imageView=1&type=webp&thumbnail=253x0`
              : null
          "
          alt=""
        />
        <span>{{ playCount(playlist.playCount) }}</span>
      </div>
      <div class="header-title">
        <h2>{{ playlist.name }}</h2>
        <span class="gd">歌单</span>
        <div class="litimg">
          <img
            :src="
              playlist?.creator?.avatarUrl
                ? `${playlist?.creator?.avatarUrl}?imageView=1&type=webp&thumbnail=30x0`
                : null
            "
            alt=""
          />
          <span>{{ this.playlist?.creator?.nickname }}</span>
        </div>
      </div>
    </header>

    <section>
      <h3>歌曲列表</h3>
      <ul class="song-list" @click="$emit('updata-song-item', songlist)">
        <li
          v-for="(song, index) in songlist"
          :key="song.id"
          @click="$emit('updata-song', song)"
          :style="{ color: song.id === songId ? 'red' : '' }"
        >
          <div class="sgleft">
            <div class="list">
              <span> {{ index + 1 }}</span>
            </div>
            <div class="text">
              <h4>{{ song.name }}</h4>
              <span>{{ song.ar[0].name }} - {{ song.al.name }}</span>
            </div>
          </div>
          <div class="sgright" v-if="song.id !== songId"></div>
          <div class="sgright active" :class="{ playing: playing }" v-else>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </li>
      </ul>
      <h5>查看更多歌曲，请下载客户端</h5>
    </section>

    <footer class="song-comment">
      <h4>精彩评论</h4>
      <ul>
        <li v-for="(songcomment, index) in songcomment" :key="index">
          <div class="top">
            <div class="left">
              <img
                :src="
                  songcomment?.user?.avatarUrl
                    ? `${songcomment?.user?.avatarUrl}?imageView=1&type=webp&thumbnail=40x0`
                    : null
                "
                alt=""
              />
              <div class="text">
                <h5>{{ songcomment.user.nickname }}</h5>
                <p>{{ songcomment.timeStr }}</p>
              </div>
            </div>
            <div class="right">
              <p>
                {{ songcomment.likedCount
                }}<svg
                  class="u-svg u-svg-unzancmt"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 28 28"
                >
                  <path
                    fill="#999"
                    d="m25.857 14.752c-.015.059-1.506 5.867-2.932 8.813-1.162 2.402-3 2.436-3.099 2.436h-12.826v-13c3 0 5.728-4 5.728-7.275 0-3.725 1.433-3.725 2.142-3.725 1.327 0 1.978 1.345 1.978 4 0 2.872-.832 4.525-.839 4.537-.161.31-.155.682.027.981.181.299.5.482.849.482h6.942c.922 0 1.551.215 1.866.64.467.626.286 1.705.164 2.112m-23.857 10.248v-10c0-1.795.659-1.981.855-2h2.145v13h-2.173c-.829 0-.827-.648-.827-1m25.309-13.54c-.713-.969-1.886-1.46-3.482-1.46h-5.519c.26-.932.519-2.285.519-4 0-5.221-2.507-6-4-6-1.909 0-4.185.993-4.185 5.725 0 2.206-1.923 5.275-3.815 5.275h-4-.011c-1.034.011-2.816.862-2.816 4v10.02c0 1.198.675 2.979 2.827 2.979h16.971.035c.364 0 3.224-.113 4.894-3.564 1.514-3.127 3.01-8.942 3.056-9.14.071-.23.664-2.289-.474-3.836"
                  ></path>
                </svg>
              </p>
            </div>
          </div>
          <div class="com">{{ songcomment.content }}</div>
        </li>
      </ul>
      <div class="collect">
        <p>收藏歌单</p>
      </div>
    </footer>
  </div>
</template>

<script>
export default {
  props: {
    songId: [Number, String],
    playing: Boolean,
  },
  data: function () {
    return {
      playlist: [],
      songlist: [],
      songcomment: [],
      loading: false,
    };
  },
  methods: {
    playCount: function (item) {
      if (item > 100000000) {
        return Math.floor(item / 10000000) + "亿";
      } else {
        return Math.floor(item / 1000) / 10 + "万";
      }
    },

    getPlayList: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: {
            id,
          },
        })
        .then((res) => {
          this.loading = false;
          this.playlist = res.data.playlist;
        });
    },

    getSongList: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/track/all", {
          params: {
            id,
            limit: 10,
            offset: 1,
          },
        })
        .then((res) => {
          this.loading = false;
          this.songlist = res.data.songs;
        });
    },

    getSongComment: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/comment/playlist", {
          params: {
            id,
          },
        })
        .then((res) => {
          this.loading = false;
          this.songcomment = res.data.hotComments;
        });
    },
  },

  created: function () {
    this.loading = true;
    this.getPlayList(this.$route.query.id);
    this.getSongList(this.$route.query.id);
    this.getSongComment(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
@keyframes running {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(60deg);
  }
}
.loading {
  width: 50rem;
  position: absolute;
  right: 0;
  left: 0;
  top: 50%;
  bottom: 0;
  margin: auto;
  z-index: 1;
}
header {
  display: flex;
  padding: 15rem;
  position: relative;
  background-size: cover;
  width: 100vw;
  height: 163rem;
  overflow: hidden;

  .bg {
    width: 100vw;
    height: 163rem;
    position: absolute;
    top: 0;
    left: 0;
    filter: blur(10rem) brightness(0.8);
    transform: scale(1.2);
    z-index: -1;
  }

  .bg::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
  }

  .header-img {
    position: relative;
    flex: 1;
    margin-right: 15rem;

    span {
      color: white;
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  .header-title {
    width: 200rem;
    h2 {
      font-size: 17rem;
      color: #fefefe;
    }

    .gd {
      position: absolute;
      top: 24rem;
      left: 15rem;
      padding: 0 8px;
      color: #fff;
      background-color: rgba(217, 48, 48, 0.8);
      border-top-right-radius: 17px;
      border-bottom-right-radius: 17px;
    }

    .litimg {
      display: flex;
      align-items: center;

      img {
        border-radius: 50%;
        border: 1px solid #fff;
      }

      span {
        color: rgb(215, 199, 199);
        font-size: 12rem;
        padding-left: 10rem;
      }
    }
  }
}

section {
  h3 {
    color: #666;
    background-color: #eeeff0;
    padding: 10rem;
  }

  ul {
    padding: 10rem;

    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1rem solid #ededed;
      .sgleft {
        padding: 8rem 0;
        display: flex;

        .list {
          width: 40rem;
          margin-left: -10rem;
          display: flex;
          justify-content: center;
          align-items: center;

          span {
            font-size: 17rem;
            color: #999;
          }
        }

        h4 {
          font-size: 17rem;
        }
        .left {
          display: inline-block;
          width: 12rem;
          height: 8rem;
          margin-right: 4rem;
          background-image: url("../imgs/index_icon_2x.png");
          background-size: 166rem 97rem;
        }

        span {
          font-size: 12rem;
          color: #888;
        }
      }

      .sgright {
        width: 22rem;
        height: 22rem;
        margin-right: 15rem;
        background-position: -24rem 0;
        background-image: url("../imgs/index_icon_2x.png");
        background-size: 166rem 97rem;

        &.active {
          background-image: none;
          display: flex;
          justify-content: space-around;

          span {
            width: 11%;
            height: 100%;
            background-color: red;
            animation: running 0.6s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;

            &:nth-child(1) {
              animation-delay: 0s;
            }
            &:nth-child(2) {
              animation-delay: -0.2s;
            }
            &:nth-child(3) {
              animation-delay: -0.4s;
            }
            &:nth-child(4) {
              animation-delay: -0.6s;
            }
          }
          &.playing {
            span {
              animation-play-state: running;
            }
          }
        }
      }
    }
  }

  h5 {
    padding: 10rem 0;
    color: #999;
    font-size: 14rem;
    text-align: center;
  }
}

footer {
  margin-bottom: 70rem;
  h4 {
    color: #666;
    background-color: #eeeff0;
    padding: 10rem;
  }

  ul {
    padding-left: 10rem;
    li {
      padding: 10rem 0;

      .top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left {
          display: flex;
          justify-content: space-between;
          align-items: center;

          .text {
            padding-left: 10rem;

            p {
              padding-top: 5rem;
            }
          }

          img {
            border-radius: 50%;
          }
        }

        .right {
          padding-right: 15rem;

          p {
            font-size: 15rem;
          }

          svg {
            width: 15rem;
            margin-left: 10rem;
          }
        }
      }

      .com {
        padding: 10rem 0;
        margin-left: 50rem;
        border-bottom: 1rem solid #ededed;
      }
    }
  }

  .collect {
    width: 100%;
    position: fixed;
    background-color: #fff;
    bottom: 0;
    text-align: center;
    padding: 15rem;

    p {
      height: 40px;
      line-height: 40px;
      border-radius: 40px;
      font-size: 18px;
      text-align: center;
      background: rgb(255, 58, 58);
      color: rgb(255, 255, 255);
    }
  }
}
</style>
