<template>
  <div class="playlyric">
    <div class="box">
      <ul ref="ul">
        <LyricText
          v-for="(p, index) in parsed"
          :key="p.index"
          :p="p"
          :currentTime="currentTime"
          :o="o"
          :index="index"
          :lyricIndex="lyricIndex"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import LyricText from "@/components/LyricText.vue";
export default {
  components: {
    LyricText,
  },
  data: function () {
    return {
      o: null,
      lyricIndex: "",
      parsed: [],
      lyric: "",
    };
  },
  props: {
    song: Object,
    currentTime: [Number, String],
    duration: Number,
  },

  created() {
    this.axios("https://apis.netstart.cn/music/lyric", {
      params: {
        id: this.song.id,
      },
    }).then((res) => {
      this.lyric = res.data.lrc.lyric;
      var arr = this.lyric.split("\n");

      this.parsed = arr
        .filter((o) => o)
        .map((o) => {
          var { groups } = o.match(
            /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/
          );
          return {
            time: groups.mm * 60 + groups.ss * 1,
            text: groups?.text?.trim(),
          };
        });
    });
  },

  methods: {
    getSonglyric: function (parsed, currentTime) {
      var tmp = [...parsed].reverse();
      var index = tmp.findIndex((o) => o.time < currentTime);
      return {
        index: tmp.length - 1 - index,
        text: tmp[index]?.text,
      };
    },
  },

  watch: {
    currentTime: function (n) {
      if (this.parsed.length) {
        this.o = this.getSonglyric(this.parsed, n);
        this.lyricIndex = this.o.index;
        this.$refs.ul.style.transform = `translateY(${
          this.o.index * -20 + 150
        }px)`;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.playlyric {
  display: flex;
  justify-content: center;
  .box {
    width: 100vw;
    height: 40vh;
    overflow: scroll;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50rem;

    ul {
      width: 100vw;
      height: 40vh;
      margin: 0;
      transform: translateY(150rem);
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}
</style>
