<template>
  <li
    class="song-item"
    @click="$emit('updata-song', record)"
    :style="{ color: record.id === songId ? 'red' : '' }"
  >
    <div class="sgleft">
      <h4>{{ record.name }}</h4>
      <div class="info">
        <div class="left"></div>
        <span>{{ record?.ar[0]?.name }} - {{ record?.al?.name }}</span>
      </div>
    </div>
    <div class="sgright" v-if="record.id !== songId"></div>
    <div class="sgright active" :class="{ playing: playing }" v-else>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    songId: [Number, String],
    playing: Boolean,
    song: Object,
    record: Object,
  },
};
</script>

<style lang="less" scoped>
@keyframes running {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(60deg);
  }
}
li.song-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 10rem;
  border-bottom: 1rem solid #ededed;

  .sgleft {
    padding: 8rem 0;

    h4 {
      font-size: 17rem;
    }

    .info {
      .left {
        display: inline-block;
        width: 12rem;
        height: 8rem;
        margin-right: 4rem;
        background-image: url("../imgs/index_icon_2x.png");
        background-size: 166rem 97rem;
      }

      span {
        font-size: 12rem;
        color: #888;
      }
    }
  }

  .sgright {
    width: 22rem;
    height: 22rem;
    margin-right: 15rem;
    background-position: -24rem 0;
    background-image: url("../imgs/index_icon_2x.png");
    background-size: 166rem 97rem;

    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;

      span {
        width: 11%;
        height: 100%;
        background-color: red;
        animation: running 0.6s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;

        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }
      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>
