<template>
  <div class="playdisc">
    <img
      v-if="song.picUrl"
      :class="{ playing: playing }"
      :src="`${song?.picUrl}?imageView=1&type=webp&thumbnail=200x0`"
      alt=""
    />
    <img
      v-else
      :class="{ playing: playing }"
      :src="`${song?.al?.picUrl}?imageView=1&type=webp&thumbnail=200x0`"
      alt=""
    />
    <img
      class="gou"
      src="../imgs/needle-ab.png"
      alt=""
      :class="{ active: playing }"
    />
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
@keyframes rotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playdisc {
  padding-top: 100rem;
  display: flex;
  justify-content: center;
  align-items: center;

  img:nth-child(1) {
    border-radius: 50%;
    animation: rotate 8s linear infinite;
    animation-play-state: paused;
    border: 30rem solid black;

    &.playing {
      animation-play-state: running;
    }
  }

  .gou {
    width: 85rem;
    border: none;
    position: absolute;
    top: 60rem;
    left: 170rem;
    z-index: 6;
    transition: all 0.5s;
  }
  .active {
    transition: all 0.5s;
    transform-origin: 10rem 40rem;
    transform: rotate(-20deg);
  }
}
</style>
